*{
  font-family: 'Roboto', sans-serif;
}
.button {
  background-color: #fff;
  border: 1px solid #d5d9d9;
  border-radius: 4px;
  box-shadow: rgba(213, 217, 217, .5) 0 2px 5px 0;
  box-sizing: border-box;
  display: inline-block;
  font-size: 13px;
  line-height: 22px;
  padding: 0;
  width: 90px;
  cursor: pointer;
}

.button:hover{
  background-color: #f7fafa;
}

#display{
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: space-evenly;
}
#settings{
  width: 10px;
  padding-left: 5px;
}
#down{
  width: 12px;
}
.navbar{
  background-color: white;
  padding: 1em;
}

body{
  background-color: #f4f4f8;
  margin: 0;
}

.card{
  background-color: white;
  width: 75%;
  margin: 1em;
  padding: 1em;
  font-size: 0.5em;
  display: flex;
  flex-direction: column;
  border-radius: 0.5em;
  border: 1px solid #d5d9d9;
  box-shadow: rgba(213, 217, 217, .5) 0 2px 5px 0;
}
.title img {
  width: 1em;
  height: 1em;
  margin-right: 0.5em;
  margin-top: 1em;
  margin-bottom: 1em;
}
.title{
  color: #282a30;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}
.id{
  color: #6c7077;
  display: flex;
  justify-content: space-between;
}
.id img{
  width: 0.8em;
}
.tag{
  display: flex;
  justify-content: flex-start;
}
.tag span{
  color: #6c7077;
  padding: 0.25em 0.4em;
  border: 0.01em solid #d5d9d9;
  border-radius: 0.24em;
  display: inline-flex;
}
.tag span img{
  width: 1em;
  margin-right: 0.5em;
}
.exclamation{
  width: 1em;
  padding: 0 0.3em;
  margin-right: 0.5em;
  border: 0.01em solid #d5d9d9;
  border-radius: 0.24em;
}

.displayOptions{
  width: 25%;
  background-color: #f4f4f8;
  border: 1px solid #d5d9d9;
  border-radius: 4px;
  box-shadow: rgba(213, 217, 217, .5) 0 2px 5px 0;
  margin-top: 1em;
  position: fixed;
  z-index: 999;
}

#displayList{
  padding: 0;
  list-style: none;
}
#displayList li{
  padding: 0;
}
#displayList li div{
  display: flex;
  justify-content: space-around;
  color: #8d8d8e;
  font-size: 0.9em;
}
.grouping{
  padding-bottom: 1em;
}
select{
  width: 35%;
  border: 1px solid #d5d9d9;
  border-radius: 4px;
  box-shadow: rgba(213, 217, 217, .5) 0 2px 5px 0;
  padding: 0.1em;
}

.usercards{
  width: 100vw;
  display: flex;
  margin-top: 1em;
  margin-left: 0.5em;
  margin-right: 0.5em;
}

.user{
  width: 20vw;
  display: flex;
  flex-direction: column;
  align-items: baseline;
}

#userimg{
  width: 0.8em;
  margin-left: 0.2em;
  margin-right: 0.5em;
}
#plus, #threedots{
  width: 0.7em;
  margin-left: 0.4em;
}
.numtasks{
  margin-left: 0.4em;
}
.userheader{
  font-size: 0.65em;
  width: 80%;
  display: flex;
  justify-content: space-between;
}